.section {
  margin: 10px;
  margin-bottom: 20px;
  background: #fff;
  color: #333;
  box-shadow: 0 2px 2px #c8c8c8;
  border-radius: 5px;
}
.section li {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 55px;
}
.section li .prefix {
  width: 30px;
  margin: 0 15px 0 10px;
  text-align: center;
  font-size: 24px;
  font-weight: 600;
}
.section li .suffix {
  color: #108ee9;
  font-size: 24px;
  margin: 0 20px;
}
.section li .title {
  flex-grow: 1;
  font-size: 16px;
}
.section li .number {
  font-size: 14px;
  color: #666;
}
.section-title {
  display: flex;
  align-items: center;
  height: 55px;
  border-bottom: 1px solid #eee;
  padding-left: 15px;
  font-size: 16px;
  color: #000;
}
.section-title div:first-child {
  flex-grow: 1;
}
.section-title div:nth-child(2) {
  font-weight: 600;
  font-size: 14px;
}
.section-title div:last-child {
  margin: 0 20px;
  font-weight: 600;
}
